import React, { Component } from 'react';
import { ReactComponent as Notebook } from '../../svg/notebook.svg';
import { ReactComponent as Grain } from '../../svg/grain.svg';
import { ReactComponent as Call } from '../../svg/call.svg';
import './BurgerMenu.scss';
import {ReactComponent as Question} from "../../svg/question.svg";

class BurgerMenu extends Component {
  constructor(props) {
    super(props);

    this.state = {
      opened: false,
    };

    this.checkboxRef = React.createRef();
    this.containerRef = React.createRef();
    this.iconRef = React.createRef();
  }

  componentDidMount() {
    document.addEventListener('mousedown', this.handleClickOutside);
  }

  componentWillUnmount() {
    document.removeEventListener('mousedown', this.handleClickOutside);
  }

  handleClickOutside = event => {
    if (event.target !== this.containerRef.current && event.target !== this.iconRef.current) {
      this.checkboxRef.current.checked = false;
    }
  };

  render() {
    return (
      <div className='burger-menu'>
        <input type='checkbox' id='checkbox' className='burger-checkbox' ref={this.checkboxRef}/>
        <label htmlFor='checkbox' className='burger-button'>
          <div className='burger-icon' ref={this.iconRef}></div>
        </label>
        <div className='burger-fade'></div>
        <div className='burger-container' ref={this.containerRef}>
          <ul className='burger-list'>
            <hr className='bigHr'/>
            <li className='burger-link'><Notebook width='20px' height='20px' viewBox="0 0 20 20"/><span><a className='link-button' href='#catalog'></a></span></li>
            <li className='burger-link'><span><a className='link-button' href='#product0' onClick={() => this.props.setProduct(0)}> </a></span></li>
            <li className='burger-link'><span><a className='link-button' href='#product1' onClick={() => this.props.setProduct(1)}>  </a></span></li>
            <li className='burger-link' ><span><a className='link-button' href='#product2' onClick={() => this.props.setProduct(2)}> </a></span></li>
            <li className='burger-link'><span><a className='link-button' href='#product3' onClick={() => this.props.setProduct(3)}> </a></span></li>
            <li className='burger-link'><span><a className='link-button' href='#product4' onClick={() => this.props.setProduct(4)}>  </a></span></li>
            <li className='burger-link'><span><a className='link-button' href='#product5' onClick={() => this.props.setProduct(5)}>  </a></span></li>
            <li className='burger-link'><span><a className='link-button' href='#product6' onClick={() => this.props.setProduct(6)}> </a></span></li>
            <hr className='bigHr'/>
            <li className='burger-link'><Grain width='20px' height='20px'/><span><a className='link-button' href='#about' onClick={this.props.scrollToAbout}>  </a></span></li>
            <hr className='bigHr'/>
            <li className='burger-link'><Call width='20px' height='20px'/><span><a className='link-button' href='#contacts' onClick={this.props.scrollToContacts}></a></span></li>
            <hr className='bigHr'/>
          </ul>
          <div className='bottom-buttons'>
            <button className='link-button question-button' href='#question' onClick={this.props.openQuestion}><Question className='question-icon' /><span> </span></button>
            <button className='black-button order-button' onClick={this.props.openOrder}> </button>
          </div>
        </div>
      </div>
    )
  }
}

export default BurgerMenu;