import React, { Component } from 'react';
import MediaQuery from 'react-responsive';
import { ReactComponent as Logo } from '../../svg/logo.svg';
import { ReactComponent as Triangle } from '../../svg/triangle.svg';
import { ReactComponent as Question } from '../../svg/question.svg';
import './Header.scss';
import BurgerMenu from "../BurgerMenu/BurgerMenu";

class Header extends Component {
  constructor(props) {
    super(props);

    this.state = {
      dropdown: false,
    };

    this.dropdownRef = React.createRef();
  }

  enterDropdown = () => {
    this.setState({
      ...this.state,
      dropdown: true,
    });
  };

  leaveDropdown = () => {
    this.setState({
      ...this.state,
      dropdown: false,
    });
  };

  chooseProduct = (index) => {
    this.props.scrollToProducts();
    this.props.setProduct(index);
  };

  render() {
    return (
      <div className='header-component'>
        <div className='header-wrapper'>
          <header className='header-bar aligned-content'>
            <div className='logo-wrapper'>
              <Logo className='logo' />
            </div>
            <MediaQuery minWidth={1024}>
              <nav>
                <a className='link-button' href='#catalog' onMouseEnter={this.enterDropdown} onMouseLeave={this.leaveDropdown} onClick={this.props.scrollToProducts} ref={this.dropdownRef}><Triangle className='triangle-icon' /></a>
                <a className='link-button' href='#about' onClick={this.props.scrollToAbout}>&nbsp; </a>
                <a className='link-button' href='#contacts' onClick={this.props.scrollToContacts}></a>
                <button className='link-button question-button' href='#question' onClick={this.props.openQuestion}><Question className='question-icon' /><span> </span></button>
                <button className='black-button order-button' onClick={this.props.openOrder}> </button>
              </nav>
            </MediaQuery>
            <MediaQuery maxWidth={1023}>
              <BurgerMenu
                openOrder={this.props.openOrder}
                openQuestion={this.props.openQuestion}
                scrollToAbout={this.props.scrollToAbout}
                scrollToContacts={this.props.scrollToContacts}
                setProduct={this.chooseProduct}
              />
            </MediaQuery>
          </header>
        </div>
        {
          this.state.dropdown &&
            (
              <div
                className='dropdown-menu'
                onMouseEnter={this.enterDropdown}
                onMouseLeave={this.leaveDropdown}
                style={{
                  marginLeft: this.dropdownRef.current.offsetLeft-24+4 // 24 -  , 4 - 
                }}
              >
                <ul>
                  <li><a className='link-button' href='#product0' onClick={() => this.chooseProduct(0)}> </a></li>
                  <li><a className='link-button' href='#product1' onClick={() => this.chooseProduct(1)}>  </a></li>
                  <li><a className='link-button' href='#product2' onClick={() => this.chooseProduct(2)}> </a></li>
                  <li><a className='link-button' href='#product3' onClick={() => this.chooseProduct(3)}> </a></li>
                  <li><a className='link-button' href='#product4' onClick={() => this.chooseProduct(4)}>  </a></li>
                  <li><a className='link-button' href='#product5' onClick={() => this.chooseProduct(5)}>  </a></li>
                  <li><a className='link-button' href='#product6' onClick={() => this.chooseProduct(6)}> </a></li>
                </ul>
              </div>
            )
        }
      </div>
    )
  }
}

export default Header;